table {
  /*border : 1px solid black;*/
  border-collapse: collapse;
  text-align: center;
  margin-left: auto; 
  margin-right: auto;
  width : 70%;
}

th, td {
  padding: 8px;
  text-align: center;
  border-bottom: 1px solid #ddd;
}
tr {
  width : 100%;
} 
/*
tbody tr:hover {
  background-color:#f5f5f5;
}*/

tbody tr::before {
  background-color:#f5f5f5;
}

table thead tr, tbody tr:nth-child(even) {
  border: 2px solid #ffa9a1;
  background-color: #ffdbd8;

  border-left : 0px;
  border-right  : 0px;
}

table tbody tr:nth-child(1) {
  background-image: url("https://cdnimg.melon.co.kr/cm/album/images/102/62/378/10262378_500.jpg");
  background-repeat: no-repeat;
  background-position: 50% 35%;
  /*opacity : 0.5;*/
  background-size: 100%;
}


html, body {
    height:100%;
    margin:0;
    overflow-x:hidden;
  }
.headerSection {
    width:100%;
    height:100px;
    /*padding:50px;*/
    background-color: #ff6f61;
  }
.searchSection{
    position: relative;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top:1%;
    margin-bottom:1%;
    width:30%;
    border:2px solid #f2f2f2;
    border-radius:100px;
    background-color: #f2f2f2;
  }
.search_bar{
    border:none;
    background-color: #f2f2f2;
    padding:1% 25%;
    margin-left:3%;
    margin-top:-3%;
  }
.searchButton{
    background-color: #f2f2f2;
    border: transparent;
    margin-top:1%;
  }
  
html, body {
    width:100%;
    margin:0;
    overflow:show;
}

